import style from "./Table.module.css";
import cn from "classnames";

export const Table = () => {
  return (
    <section className={style.section}>
      <p className={style.tableItem}></p>
      <p className={cn(style.tableItem, style.centerItem)}></p>
      <p className={cn(style.tableItem, style.centerItem)}></p>
      <p className={style.tableItem}></p>
      <p className={style.tableItem}></p>
      <p className={style.tableItem}></p>
      <p className={cn(style.tableItem, style.description)}></p>
    </section>
  );
};
